import { useRef } from "react";
import { useCallback } from "react";

export const useDebounse = (cb, delay) => {
  //   useDebounse
  const currentRef = useRef(null); //             currnet
  const debouncedCb = useCallback(
    (...args) => { //  ...args -       useDebounce
      if (currentRef.current) { //   urrentRef     
        clearTimeout(currentRef.current);
      }
      currentRef.current = setTimeout(() => { //  
        console.log("ARGS", ...args);
        cb(...args);
      }, delay);
    },
    [cb, delay]
  );
  return debouncedCb;
};
